@import url("https://www.w3schools.com/w3css/4/w3.css");

.w3-modal {
    background-color: rgba(0, 0, 0, .6);
}

#dialog-section {
  position: relative;
}

#travel-dialog,
#add-dialog,
#edit-dialog,
#delete-dialog {
  display: block;
}

#delete-content {
  margin-bottom: 10px;
}

#dialog-content {
  margin-left:10px;
}

#img-prev {
  max-width: 100%;
}

#delete-dialog button {
  margin-right: 10px;
}

#travel-dialog img {
  display: block;
  width: 90%;
  max-width: 700px;
  height: 380px;
  object-fit: cover;
  margin: 16px auto;
  border-radius: 8px;
}

#edit-links a {
  margin-left: 10px;
  font-size: 20px;
  text-decoration: none;
  cursor: pointer;
  z-index: 9999;
  position: relative;
}

#edit-links {
  display: flex;
  align-items: center;
}


@media only screen and (min-width: 600px) {
  #img-prev-section {
    flex: 1;
  }

  #img-prev {
    max-width: 100%;
  }

  #img-upload {
    flex: 6;
  }
}